import React, { useEffect, useState } from 'react'
import { Button, Container, TextField, Typography } from '@material-ui/core';
import { ArrowBack } from '@material-ui/icons';
import { useHistory, useLocation } from 'react-router';


const Unsubscribe = () => {
    const [email, setEmail] = useState("");
    const [valid, setValid] = useState(true);
    const [success, setSuccess] = useState(false);
    const history = useHistory();
    const location = useLocation();

    useEffect(() => {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(String(email).toLowerCase())) {
            setValid(true);
        } else {
            setValid(false)
        }
    }, [email]);

    const handleLink = (link) => {
        history.push({ pathname: link, search: location.search});
    }

    const unsubscribe = () => {
        if (valid && email !== "")
            setSuccess(true)
    }

    return (
        <Container style={{display: 'flex', justifyContent: 'center', height: '90vh'}}>
            <div style={{alignSelf: 'center', maxWidth: '50%'}}>
                <Button style={{marginBottom: '2rem'}} variant="contained" color="primary" onClick={() => handleLink("/")}><ArrowBack />&nbsp; Go back</Button>
                <Typography>
                    To unsubscribe from Vilkkoni's newsletter, please input your email address below. You will be sent a confirmation email with an unsubscribe link
                </Typography>
                {success ? (
                    <Typography style={{marginTop: '2rem', textAlign: 'center'}}>
                        SUCCESS
                    </Typography>
                ) : (
                    <div>
                        <TextField
                            style={{margin: '1rem 0'}}
                            autoFocus
                            margin="dense"
                            id="email"
                            label="Email Address"
                            type="email"
                            fullWidth
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            error={!valid}
                            helperText={ valid ? null : "Invalid email" }
                        />
                        <Button color="primary" style={{float: 'right'}} onClick={unsubscribe} >Unsubscribe</Button>
                    </div>
                )}
            </div>
        </Container>
    )
};

export default Unsubscribe;